import PopoverMenu from '@nextcloud/vue/dist/Components/PopoverMenu';
import Actions from '@nextcloud/vue/dist/Components/Actions';
import ActionButton from '@nextcloud/vue/dist/Components/ActionButton';
import ClickOutside from 'vue-click-outside';
import { getCurrentUser } from '@nextcloud/auth';
import UserRowMixin from '../../mixins/UserRowMixin';
export default {
  name: 'UserRowSimple',
  components: {
    PopoverMenu,
    ActionButton,
    Actions
  },
  directives: {
    ClickOutside
  },
  mixins: [UserRowMixin],
  props: {
    user: {
      type: Object,
      required: true
    },
    loading: {
      type: Object,
      required: true
    },
    showConfig: {
      type: Object,
      required: true
    },
    userActions: {
      type: Array,
      required: true
    },
    openedMenu: {
      type: Boolean,
      required: true
    },
    feedbackMessage: {
      type: String,
      required: true
    },
    subAdminsGroups: {
      type: Array,
      required: true
    },
    settings: {
      type: Object,
      required: true
    }
  },
  computed: {
    userGroupsLabels() {
      return this.userGroups.map(group => group.name).join(', ');
    },

    userSubAdminsGroupsLabels() {
      return this.userSubAdminsGroups.map(group => group.name).join(', ');
    },

    usedSpace() {
      if (this.user.quota.used) {
        return t('settings', '{size} used', {
          size: OC.Util.humanFileSize(this.user.quota.used)
        });
      }

      return t('settings', '{size} used', {
        size: OC.Util.humanFileSize(0)
      });
    },

    canEdit() {
      return getCurrentUser().uid !== this.user.id || this.settings.isAdmin;
    },

    userQuota() {
      let quota = this.user.quota.quota;

      if (quota === 'default') {
        quota = this.settings.defaultQuota;

        if (quota !== 'none') {
          // convert to numeric value to match what the server would usually return
          quota = OC.Util.computerFileSize(quota);
        }
      } // when the default quota is unlimited, the server returns -3 here, map it to "none"


      if (quota === 'none' || quota === -3) {
        return t('settings', 'Unlimited');
      } else if (quota >= 0) {
        return OC.Util.humanFileSize(quota);
      }

      return OC.Util.humanFileSize(0);
    }

  },
  methods: {
    hideMenu() {
      this.$emit('hide-menu');
    },

    toggleEdit() {
      this.$emit('update:editing', true);
    }

  }
};